/* ============================ */
/* Modern Portfolio CSS - Clean */
/* ============================ */

/* 1. Reset & Base */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
  }
  
  body {
    background: #b5b6b9; /* light, soothing */
    color: #1E293B; /* primary text */
    line-height: 1.6;
    padding-inline: 5%; /* left/right margins */
  }
  
  /* Links */
  a {
    color: #4ACAD5; /* primary accent */
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  a:hover {
    color: #7ED957; /* secondary accent */
  }
  
  /* Header */
  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding-block: 30px;
  }
  
  header h1 {
    font-weight: 700;
    font-size: 1.8rem;
  }
  
  header nav a {
    margin-left: 25px;
    font-weight: 500;
    font-size: 1rem;
  }
  
  /* Hero Section */
  .hero {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 80px auto;
    gap: 60px;
  }
  
  .hero-text {
    flex: 1 1 600px;
  }
  
  .hero-text h2 {
    font-size: 2.8rem;
    font-weight: 800;
    margin-bottom: 20px;
  }
  
  .hero-text p {
    font-size: 1.1rem;
    color: #475569;
    margin-bottom: 30px;
  }
  
  .hero-buttons a {
    display: inline-block;
    margin-right: 15px;
    padding: 12px 28px;
    border-radius: 14px;
    font-weight: 600;
    transition: all 0.3s ease;
  }
  
  .hero-buttons .primary {
    background: #4ACAD5;
    color: #fff;
  }
  
  .hero-buttons .primary:hover {
    background: #7ED957;
    transform: translateY(-2px);
  }
  
  .hero-buttons .secondary {
    border: 2px solid #4ACAD5;
    color: #4ACAD5;
  }
  
  .hero-buttons .secondary:hover {
    background: #4ACAD5;
    color: #fff;
    transform: translateY(-2px);
  }
  
  /* Social Icons in Hero */
  .hero .social-icons {
    margin-top: 20px;
  }
  
  .social-icons a {
    margin-right: 15px;
    font-size: 1.4rem;
    color: #4ACAD5;
  }
  
  .social-icons a:hover {
    color: #7ED957;
  }
  
  /* Profile Card */
  .profile-card {
    flex: 0 0 280px;
    background: #FFFFFF;
    border-radius: 20px;
    padding: 25px;
    text-align: center;
    box-shadow: 0 8px 20px rgba(0,0,0,0.05);
  }
  
  .profile-card img {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    object-fit: cover;
    margin-bottom: 15px;
  }
  
  .profile-card h3 {
    font-size: 1.3rem;
    font-weight: 700;
    margin-bottom: 5px;
  }
  
  .profile-card p {
    font-size: 0.95rem;
    color: #475569;
    margin-bottom: 15px;
  }
  
  .profile-card a {
    display: inline-block;
    margin-top: 10px;
    font-weight: 500;
    color: #4ACAD5;
  }
  
  .profile-card a:hover {
    color: #7ED957;
  }
  
  /* Section Titles */
  section h3, section h4 {
    font-size: 1.8rem;
    font-weight: 700;
    margin-bottom: 25px;
    color: #1E293B;
  }
  
  /* Card Grid */
  .card-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 25px;
    margin-bottom: 40px;
  }
  
  /* Cards */
  .card, .experience-card {
    background: #FFFFFF;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 0 10px 20px rgba(0,0,0,0.05);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .card:hover, .experience-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 25px rgba(0,0,0,0.08);
  }
  
  .card h5, .experience-card h4 {
    font-size: 1.15rem;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  .card p, .experience-card p {
    font-size: 0.95rem;
    color: #475569;
    line-height: 1.5;
  }
  
  /* Footer */
  footer {
    text-align: center;
    padding: 60px 20px;
    color: #475569;
    background: #F5F7FA;
  }
  
  footer h4 {
    font-size: 1.3rem;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  footer .social-icons a {
    font-size: 1.4rem;
  }
  
  footer p {
    margin-top: 15px;
    font-size: 0.85rem;
  }
  
  /* Fade-in animation */
  @keyframes fadeInUp {
    0% { opacity: 0; transform: translateY(15px); }
    100% { opacity: 1; transform: translateY(0); }
  }
  
  .hero-text, .card, .experience-card, .profile-card {
    animation: fadeInUp 0.8s ease forwards;
  }
  